<template>
  <div id="apps">
    <Loading />
    <div v-if="loaded" id="main">
      <div :class="['video-wrap', isCssFull ? 'css-full' : '']">
        <WhiteBoard ref="whiteboard" :isPptAsMain="isPptAsMain" v-if="isPptAsMain&&isBB" />
        <div v-else></div>
        <!-- <Player v-else ref="player" /> -->
        <!-- <Barrage ref="barrage" /> -->
        <button style="position: absolute" v-if="isCssFull" @click="isCssFull = false"></button>
      </div>
      <!-- icon -->
      <Menu :isPptAsMain="isPptAsMain" ref="menu" @buttonPress="handleBottomPress($event)" />
      <!--  -->
      <div class="media-panel">
        <div class="notice" :class="{'notice-show': showNotice}">
          <Notice v-bind="noticeProps" />
        </div>
        <InteractionPanel :isPptAsMain="isPptAsMain" />
      </div>
      <!--  -->
      <ModalPanel />
      <!--  -->
      <div v-show="isRollCalling" class="roll-call">
        <p class="roll-call-tip">{{rollCallCountDown}}</p>
        <span class="roll-call-btn" @click="answerRollCall()"></span>
      </div>
      <!--  -->
      <div id="interaction-placeholder">
        <div class="redpacket">
          <RedPacket v-bind="redpacketProps" />
        </div>
        <LotteryHit />
      </div>

      <div id="attention-detection-container" style="display: none">

      </div>
    </div>
  </div>
</template>

<script>
import Loading from "../components/Loading";
import Player from "../components/Player";
import Barrage from "../components/Barrage";
import Menu from "../components/Menu";
import InteractionPanel from "../components/InteractionPanel";
import WhiteBoard from "../components/InteractionPanel/WhiteBoard";
import ModalPanel from "../components/ModalPanel";
import LotteryHit from "../components/LotteryHit";

var eventEmitter = BJY.eventEmitter;

export default {
  name: "Apps",
  components: {
    Loading,
    Player,
    Menu,
    Barrage,
    WhiteBoard,
    /**
     * .vue
     * Yox.transformOptionssdkvue
     * main.jsvueYox.vue
     * createprops
     */
    Notice: Yox.transformOptions(BJY.Notice),
    InteractionPanel,
    ModalPanel,
    RedPacket: Yox.transformOptions(BJY.Interaction),
    LotteryHit,
  },
  data() {
    return {
      loaded: false,
      // noticecreate
      noticeProps: {
        element: $(".media-panel .notice")[0], // 
        replace: false,
        defaultContent: "...",
        // foldDefault: false // 
        foldDefault: true, //  ,
      },
      redpacketProps: {
        element: $(".interaction-placeholder .redpacket")[0], // 
        needRedPacket: true,
        canGetRedPacket: true,
        canSendRedPacket: false,
        redPacketOptions: {
          skin: "bjy",
          hasRankingList: true,
        },
      },
      isCssFull: false,
      showNotice: false,
      // pptppttrue
      isPptAsMain: false,
      isRollCalling: false,
      // 
      rollCallCountDown: 10,
      rollCallTimer: null,
      isBB: false,
    };
  },
  created() {
    console.log(
      "******************************************************************************"
    );
    console.log("*    BJY JS SDK -  - " + BJY.version);
    console.log("*   API https://www.baijiayun.com/js-sdk/doc/#/README");
    console.log(
      "*   https://www.baijiayun.com/js-sdk/doc/#/change-log"
    );
    console.log(
      "*   https://www.baijiayun.com/js-sdk/doc/#/live?id=%e5%b8%b8%e8%a7%81%e9%97%ae%e9%a2%98"
    );
    console.log(
      "*   H5 Demo PCm3u8 webrtcwebrtc: 0"
    );
    console.log("*   signhttps://dev.baijiayun.com/wiki/detail/1");
    console.log(
      "******************************************************************************"
    );
    var options = {
      prefixName: "e74839313",
      env: "production",
      room_id: "21030980034604",
      user_number: 18700,
      user_avatar: "https://img.baijiayun.com/0bjcloud/live/avatar/v2/121.jpg",
      user_name: "test001",
      user_role: 0,
      sign: "f6264e2128cea5800d1e22b37a2a8c30",
      webrtc: 0,
    };

    // debug 
    // webrtcurl &webrtc=0
    if (location.href.includes("debug=1")) {
      var url = prompt(
        ",webrtc&webrtc=1"
      );
      options = Object.assign(options, this.urlParser(url));
      console.log(options);
    }

    var classOption = {
      // 
      id: options.room_id,
    };

    if (Number(options.webrtc)) {
      classOption.webrtcType = Number(options.webrtc);
    }

    BJY.init({
      env: options.env,
      privateDomainPrefix: options.prefixName,
      class: classOption,
      user: {
        number: options.user_number,
        avatar: decodeURIComponent(options.user_avatar),
        name: decodeURIComponent(options.user_name),
        type: Number(options.user_role),
      },
      teacher: {
        type: 1,
      },
      sign: options.sign,
    });

    eventEmitter
      .on(eventEmitter.CLASSROOM_CONNECT_TRIGGER, function (event, data) {
        // 
      })
      .on(eventEmitter.VIEW_RENDER_TRIGGER, (event, data) => {
        // BJY.init()
        this.loaded = true;
        this.isBB = true;
        console.log(BJY.MessageSender);
        this.$nextTick(function () {
          BJY.AttentionDetection.create({
            element: $("#attention-detection-container"),
            replace: false,
            skin: "light",
          });
        });
      })
      .on(eventEmitter.CLASS_END, () => {
        console.log("");
        // this.loaded = false;
        this.isBB = false;
      })
      .on(eventEmitter.CLASS_START, () => {
        console.log("");
        this.isBB = true;
      })
      //  loading 
      .one(eventEmitter.LOADING_PANEL_END, (event, data) => {
        // loading 
        $("#main").css({
          top: 0,
          bottom: 0,
        });
      })
      .on(eventEmitter.LOGIN_CONFLICT, () => {
        alert("user_number");
      })
      // 
      .on(
        eventEmitter.ROLL_CALL, // 
        (e, data) => {
          this.rollCallCountDown = data.duration;
          this.isRollCalling = true;
          this.rollCallTimer = setInterval(() => {
            if (this.rollCallCountDown === 0) {
              clearInterval(this.rollCallTimer);
              this.isRollCalling = false;
            }
            this.rollCallCountDown--;
          }, 1000);
        }
      )
      // 
      .on(eventEmitter.NOTICE_CHANGE, (e, data) => {
        console.log(data);
        setTimeout(() => {
          this.checkNeedScroll();
        }, 500);
      })
      // 
      .on(eventEmitter.NOTICE_RES, (e, data) => {
        console.log(data);
      })
      .on(eventEmitter.ATTENTION_DETECTION_ALERT, function (event, data) {
        if (data.content) {
          alert(data.content);
        }
      });
  },
  beforeDestroy() {
    //eventEmitter.trigger(eventEmitter.CLASSROOM_EXIT_TRIGGER);
    BJY.dispose(BJY.config.BJY_DISPOSE_TYPE_OUT_ROOM);
  },
  methods: {
    checkNeedScroll() {
      var wrapWidth = $(".bjy-content-container .bjy-content").width();
      var textWidth = $(".bjy-content-container .bjy-content")
        .children()
        .width();
      if (textWidth >= wrapWidth) {
        $(".media-panel .bjy-notice").addClass("need-scroll");
      } else {
        $(".media-panel .bjy-notice").removeClass("need-scroll");
      }
    },
    answerRollCall() {
      eventEmitter.trigger(eventEmitter.ROLL_CALL_RES);
      this.isRollCalling = false;
      clearInterval(this.rollCallTimer);
    },
    // url
    urlParser(url) {
      var params = {};
      if (url) {
        try {
          // query
          var queryStr = url.split("?")[1];
          var paramList = queryStr.split("&");
          paramList.forEach((item) => {
            var pair = item.split("=");
            params[pair[0]] = pair[1];
          });
          // 
          params.prefixName = url.split(".")[0].split("//")[1];
          // 
          params.env = this.getEnv(url);
        } catch (e) {
          console.log(e);
        }
      }
      return params;
    },
    getEnv(url) {
      // 
      const hostName = url.match(/\/\/(.*)\./)[1];
      if (
        /test/.test(hostName) ||
        /localhost/.test(hostName) ||
        /\d{1,3}\.\d{1,3}\.\d{1,3}/.test(hostName)
      ) {
        return "test";
      }
      if (/beta/.test(hostName)) {
        return "beta";
      }
      if (/www/.test(hostName)) {
        return "";
      }
      return "";
    },
    // 
    handleBottomPress(e) {
      switch (e) {
        case "notice":
          this.noticeToggle();
          break;

        case "barrage":
          this.barrageToggle();
          break;

        case "refresh":
          this.refresh();
          break;

        case "fullscreen":
          this.fullScreen();
          break;

        case "swap":
          this.swapPptVideo();
          eventEmitter.trigger(eventEmitter.WINDOW_RESIZE);
      }
    },
    swapPptVideo() {
      var videoDom = document.getElementById("h5-player");
      var pptDom = document.getElementById("whiteboard");

      var videoParentNode = videoDom.parentNode;
      var pptParentNode = pptDom.parentNode;
      var videoPlaceholderNode = document.createElement("span");
      var pptPlaceholderNode = document.createElement("span");

      videoParentNode.insertBefore(videoPlaceholderNode, videoDom);
      pptParentNode.insertBefore(pptPlaceholderNode, pptDom);

      pptDom && videoParentNode.insertBefore(pptDom, videoPlaceholderNode);
      videoDom && pptParentNode.insertBefore(videoDom, pptPlaceholderNode);

      videoParentNode.removeChild(videoPlaceholderNode);
      pptParentNode.removeChild(pptPlaceholderNode);
    },
    noticeToggle() {
      this.showNotice = !this.showNotice;
      this.checkNeedScroll();
    },
    barrageToggle() {
      this.$refs.barrage.toggle();
      const isBarrageOn = this.$refs.barrage.isBarrageOn();
      this.$refs.menu.toggleIcon(isBarrageOn);
    },
    refresh() {
      window.player && window.player.play();
    },
    fullScreen() {
      var fullDom = document.getElementsByClassName("video-wrap")[0]
        .firstElementChild;
      this.toFullVideo(fullDom);
    },
    toFullVideo(fullDom) {
      if ($(fullDom).find("video").length) {
        fullDom = $(fullDom).find("video")[0];
      } else if (
        navigator.userAgent.indexOf("iPhone") !== -1 &&
        $(fullDom).find(".bjy-whiteboard").length
      ) {
        this.handleCssFull(fullDom);
        return;
      }
      if (fullDom.requestFullscreen) {
        return fullDom.requestFullscreen();
      } else if (fullDom.webkitRequestFullScreen) {
        return fullDom.webkitRequestFullScreen();
      } else if (fullDom.mozRequestFullScreen) {
        return fullDom.mozRequestFullScreen();
      } else if (fullDom.webkitEnterFullscreen) {
        return fullDom.webkitEnterFullscreen();
      } else {
        return fullDom.msRequestFullscreen();
      }
    },
    handleCssFull(fullDom) {
      this.isCssFull = true;
    },
  },
};
</script>

<style lang="scss">
@import "../assets/iconfont/iconfont.css";
#apps {
  height: 100%;
  width: 100%;
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  #main {
    position: absolute;
    left: 0;
    right: 0;
    top: -10000px;
    bottom: -2000px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    .video-wrap {
      width: 100%;
      min-height: 210px;
      position: relative;

      &.css-full {
        width: 100%;
        height: 100%;
        position: fixed;
        z-index: 2;
        background: #fff;
      }

      #h5-player {
        height: 210px !important;
      }

      .bjy-player-container,
      .bjy-container {
        display: flex;
        align-items: center;
      }

      .bjy-barrage {
        z-index: 10;
      }
    }

    .media-panel {
      flex-grow: 1;
      width: 100%;
      position: relative;
      overflow: hidden;

      .notice {
        position: absolute;
        top: 0;
        width: 100%;
        padding: 0;
        height: 36px;
        box-sizing: border-box;
        background: #fff;
        transition: 0.3s left;
        left: 100%;
        z-index: 1;

        .bjy-notice {
          background: #fff;
          padding: 0 8px;
        }
      }

      .notice-show {
        left: 0;
      }

      .need-scroll {
        .bjy-content-container[data-fold="1"] {
          .bjy-text {
            animation: 10s wordsLoop linear infinite normal;
          }
          a {
            display: inline-block;
            animation: 10s wordsLoop linear infinite normal;
          }
        }
      }
    }

    .roll-call {
      position: absolute;
      top: 50%;
      background: #fff;
      border-radius: 5px;
      border: 1px solid #ddd;
      text-align: center;

      .roll-call-tip {
        border-bottom: 1px solid #ddd;
        padding: 0 20px;
        margin: 0;
        line-height: 36px;
      }

      .roll-call-btn {
        color: #1795ff;
        line-height: 36px;
      }
    }

    .bjy-interaction {
      .bjy-packet-fall-keyframe.bjy-hidden {
        display: block;
      }

      .bjy-interaction-red-packet {
        z-index: 3;

        .bjy-animation-canvas {
          touch-action: none;
        }

        .bjy-result-of-get {
          animation: redPacketResultScaleIn-h5 1s;
          transform: scale(0.7);
        }

        .bjy-ranking-list {
          transform: scale(0.7);

          .bjy-close {
            transform: scale(1.5);
          }

          .bjy-body .bjy-table .bjy-item:nth-child(2) td:nth-child(2) {
            width: 43%;
          }
        }
        .bjy-interlude {
          transform: scale(0.7) translateY(-140px);
        }
        .bjy-hit-animation {
          transform: scale(0.6) translate(-65%, -40%);
        }
      }
    }
  }
}

@keyframes redPacketResultScaleIn-h5 {
  from {
    transform: scale(0);
    opacity: 0;
  }
  to {
    transform: scale(0.7);
    opacity: 1;
  }
}

@keyframes wordsLoop {
  0% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
  }
  20% {
    transform: translateX(0px);
    -webkit-transform: translateX(0px);
  }
  100% {
    transform: translateX(-100%);
    -webkit-transform: translateX(-100%);
  }
}
</style>
