<template>
    <div class="barrage-wrap"></div>
</template>

<script>
export default {
  components: {},
  data () {
    return {
      barrage: null
    }
  },
  computed: {},
  watch: {},
  methods: {
    open () {
      this.barrage.open()
    },
    close () {
      this.barrage.close()
    },
    isBarrageOn () {
      return this.barrage.isOpened()
    },
    toggle () {
      if (this.isBarrageOn()) {
        this.close()
        this.$Toast('')
      } else {
        this.open()
        this.$Toast('')
      }
    }
  },
  created () {},
  mounted () {
    // 
    this.barrage = new BJY.Barrage({
    // 
      container: $('.video-wrap'),
      // 
      speed: 100,
      // 
      maxLength: 20,
      //  top - bottom 
      top: 30,
      bottom: 30,
      // 
      trackHeight: 30,
      // 
      textOnly: false,
      // 
      renderContent: function (data) {
        return data.content
      }
    })
  },
  beforeDestroy () {}
}
</script>

